<template>
  <div id="create-new">
    <mynav></mynav>
    <h2 class="title">
      <p>
        <router-link :to="'/project/' + projectid + '/patients/1'" style="margin-right: 14px;"><i class="iconfont icon-left"></i></router-link>
        {{ $root.currentProject.u_projectName }}
      </p>
    </h2>
    
    <!--侧边栏-->
    <div class="fixed-right" :style="{left: content_width + 'px'}">
      <div class="temp-menu">
        <div v-for="t in templates" :class="{active: t.u_templateid === templateid}" @click="changeTemplate(t.u_templateid)">
          <p>{{ t.u_templatename }}</p>
          <!--<span :class="{active: t.u_templateid === templateid}"></span>-->
        </div>
      </div>
      <router-link :to="'/project/' + projectid + '/patients/1'" class="back-btn btn">创建新病历</router-link>
    </div>

    <!--主体内容，病历记录卡片-->
    <div class="content" id="records">
      <div class="card add-record-area" v-if="addState">
        <div class="card-header flex-row" style="justify-content: space-between; align-items: center;">
          <h3 v-for="name in templates" v-if="name.u_templateid === templateid">{{ name.u_templatename }}</h3>
        </div>
        <transition name="fade">
          <component class="input-area" v-bind:is="mycomponent" :mdid="mdid" :templateid="templateid" :isnew="true" @putsever="setRecords(arguments[0])"></component>
        </transition>
        <transition name="fade">
          <record-bar :imgshow="imgState" :remarkshow="remarkState" @putsever="setRemarks(arguments[0])"></record-bar>
        </transition>
        <div class="bar flex-row">
          <div class="flex-row">
            <i class="iconfont icon-beizhu add-remark" @click="remarkState = true"></i>
            <i class="iconfont icon-tupian add-remark" @click="imgState = true"></i>
          </div>
          <div>
            <m-button type="gray" style="margin-right: 24px;" @click="cancelInputState">取消</m-button>
            <m-button type="blue" :loading="addBtnState" @click="putData">添加</m-button>
          </div>
        </div>
      </div>

      <div class="cards flex-col">
        <div class="card base-info">
          <p>姓名： <span>{{ patientBase.u_patientname }}</span></p>
          <p>性别： <span v-if="patientBase.u_gender==='1'">男</span> <span v-if="patientBase.u_gender==='2'">女</span></p>
          <p>出生日期： <span>{{ patientBase.u_birthday }}</span></p>
          <p>入组时间： <span>{{ patientBase.u_jointime }}</span></p>
          <p>姓名缩写： <span>{{ shortName | nameShort }}</span></p>
          <div class="card-footer">
            <span style="font-size: 12px; color: #777;">{{ patientBase.u_createtime | formatDate }}</span>
          </div>
        </div>
        <transition-group name="list">
          <div class="card" v-for="(r, index) in records" v-bind:key="r">
            <div class="card-header flex-row" style="justify-content: space-between; align-items: center;">
              <h3>{{ r.type }}</h3>
              <div class="flex-row">
                <dropdown>
                  <span class="button tools" slot="trigger">操作<i class="iconfont icon-bottom"></i></span>
                  <li @click="changeRecord(r.recordid, r.templateid)">编辑</li>
                  <li @click="delRecord(r.recordid, index)">删除</li>
                  <li @click="changeTemplate(r.templateid)">添加新记录</li>
                </dropdown>
              </div>
            </div>
            <div class="card-main">
              <p v-if="val != ''" v-for="(val, index) in r.data" class="flex-row">
                <span class="main-record">{{ index + '：' }}</span>
                <span v-text="val" style="flex: 1;"></span>
              </p>
            </div>
            <div class="card-remark text flex-row" v-if="r.remark != ''" >
              <p>{{ r.remark }}</p>
            </div>
            <div class="card-remark imgs flex-row">
              <img :src="'http://' + url" class="image-btn"  v-for="url in r.images" v-imgshow/>
            </div>
            <div class="card-footer">
              <span style="font-size: 12px; color: #777;">{{ r.update_time | formatDate }}</span>
            </div>
          </div>
        </transition-group>
      </div>
    </div>

  </div>
</template>

<script>
  import Vue from 'vue'
  import API from '../api.js'
  import { scrollTo, Type } from '../utils/tools.js'
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'
  import Loading from '../lib/Loading'
  import Temp from '../template/index'
  import PatientBase from '../components/PatientBase'
  import RecordBar from '../components/RecordBar.vue'
  import Dropdown from '../lib/Dropdown.vue'

  export default {
    name: 'Create',
    components: {
      Mynav,
      Dropdown,
      PatientBase,
      RecordBar,
      Loading,
      MButton
    },
    data () {
      return {
        content_width: '',
        addBtnState: false,
        userid: '',
        projectid: '',
        mdid: '',
        templateid: '',
        addState: false,
        remarkState: false,
        imgState: false,
        postdata: {
          source_data: {},
          patientdata: {},
          imgs: '',
          remark: ''
        },
        templates: [],
        mycomponent: '',
        patientBase: {},
        shortName: '',    // 患者姓名拼音缩写
        records: []
      }
    },
    mounted () {
      this.projectid = this.$route.params.projectid
      this.mdid = this.$route.params.mdid
      this.content_width = document.getElementById('records').offsetLeft + document.getElementById('records').offsetWidth + 16
      window.addEventListener('resize', () => {
        this.content_width = document.getElementById('records').offsetLeft + document.getElementById('records').offsetWidth + 16
      }, false)
      this.getTemplates()
      this.getPatientBase()
      this.getPatientRecords()
    },
    methods: {
      /** 获取患者基本信息 */
      getPatientBase () {
        API.GetPatientBase({mdid: this.mdid}).then(response => {
          this.patientBase = response[0]
          this.nameToshort(response[0].u_patientname)
        }).catch(err => {
          console.log(err)
        })
      },
      nameToshort (str) {
        API.ChineseToShort({string: str}).then(response => {
          this.shortName = response.shortpinyin
        }).catch(err => {
          console.log(err)
        })
      },
      /** 获取患者病历记录列表 */
      getPatientRecords () {
        this.records = []
        API.GetPatientRecords({mdid: this.mdid}).then(response => {
          response.map(n => this.records.push({
            type: n.u_templatename,
            recordid: n.u_recordid,
            templateid: n.u_templetid,
            create_time: n.u_createtime,
            update_time: n.u_updatetime,
            data: JSON.parse(n.u_patientdata),
            /* eslint-disable */
            images: eval('{'+ n.imgs +'}'),
            /* eslint-endable */
            remark: n.remark
          }))
        }).catch(err => {
          console.log(err)
        })
      },
      /** 获取项目模板 */
      getTemplates () {
        API.GetpProjectTemplates({projectid: this.projectid}).then(response => {
          this.templates = response
          if (this.newPatientState()) {
            this.changeTemplate(this.templates[0].u_templateid)
          }
        }).catch(err => {
          console.log(err)
        })
      },
      /** 新建患者直接进入编辑页面 */
      newPatientState () {
        return this.$route.query.new === '1'
      },
      /** 切换模板 */
      changeTemplate (id) {
        scrollTo(document.body, 0, 300)
        // 初始化状态
        this.cleanTempState()
        // 修改模板
        this.addState = true
        this.mycomponent = ''
        Vue.component('temp', Temp['template' + id])
        this.mycomponent = 'temp'
        this.templateid = id
      },
      /* 关闭输入模式 */
      cancelInputState () {
        let vm = this
        if (this.postdata.imgs.length > 0 || this.postdata.remark.length > 0 || Object.keys(this.postdata.patientdata).length !== 0) {
          this.confirm({
            title: '退出编辑状态',
            message: '退出编辑状态会清空现在已经输入的数据！',
            onConfirm () {
              vm.cleanTempState()
            }
          })
        } else {
          this.cleanTempState()
        }
      },
      /** 切换下一个模板 */
      nextTemplate () {
        let index = 0
        for (let n in this.templates) {
          if (this.templateid === this.templates[n].u_templateid) {
            index = parseInt(n) + 1
            break
          }
        }
        if ((this.templates.length - 1) >= parseInt(index)) {
          this.changeTemplate(this.templates[index].u_templateid)
        } else {
          this.cleanTempState()
        }
      },
      /* 清除模板状态 */
      cleanTempState () {
        this.addState = false
        this.templateid = ''
        this.remarkState = false
        this.imgState = false
        this.postdata.imgs = ''
        this.postdata.remark = ''
        this.postdata.patientdata = {}
        this.postdata.source_data = {}
      },
      setRemarks (data) {
        this.postdata.imgs = JSON.stringify(data.imgs)
        this.postdata.remark = data.remark
      },
      setRecords (data) {
        console.log(data)
        let isHaveData = false
        for (let i in data.sourceData) {
          if (Type(data.sourceData[i]) === 'string' || Type(data.sourceData[i]) === 'array') {
            if (data.sourceData[i].length > 0) { 
              isHaveData = true
              break
            }
          } else {
            isHaveData = true
          }
        }
        if (isHaveData) {
          this.postdata.source_data = data.sourceData
          this.postdata.patientdata = data.showData
        } else {
          this.postdata.source_data = {}
          this.postdata.patientdata = {}
        }
      },
      postRecordData () {
        return {
          createuserid: this.$root.userid,
          mdid: this.mdid,
          templateid: this.templateid,
          patientdata: JSON.stringify(this.postdata.patientdata),
          sourcedata: JSON.stringify(this.postdata.source_data),
          imgs: this.postdata.imgs,
          remark: this.postdata.remark
        }
      },
      putData () {
        // 按钮点击不可点击状态
        this.addBtnState = true
        const data = this.postRecordData()
        if (data.sourcedata === '{}') {
          this.toast({
            text: '新纪录没有任何数据，请添加数据后保存',
            type: 'error',
            placement: 'bottom-left'
          })
          this.addBtnState = false
          return
        }
        API.CreatePatientData(data).then(response => {
          this.toast({
            text: '新纪录添加成功',
            type: 'success',
            placement: 'bottom-left'
          })
          // 按钮恢复可点击
          this.addBtnState = false
          this.nextTemplate()
          this.getPatientRecords()
        }).catch(err => {
          // 按钮恢复可点击
          this.addBtnState = false
          this.toast({
            text: '创建失败，请重新尝试',
            type: 'error',
            placement: 'bottom-left'
          })
          console.log(err)
        })
      },
      delRecord (recordid, index) {
        API.DeletePatientData({userid: this.$root.userid, recordid: recordid}).then(response => {
          this.toast({
            text: '删除成功',
            type: 'success',
            placement: 'bottom-left'
          })
          this.records.splice(index, 1)
        }).catch(err => {
          this.toast({
            text: '删除失败，请重新尝试',
            type: 'error',
            placement: 'bottom-left'
          })
          console.log(err)
        })
      },
       /** 跳转到修改记录页面 */
      changeRecord (recordid, templateid) {
        this.$router.push({name: 'Record', params: {projectid: this.projectid, mdid: this.mdid, templateid: templateid, recordid: recordid}})
      }
    }
  }
</script>

<style scoped>
  .title {
    background: #468df1;
    height: 48px;
    color: #fff;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 48px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.23);
  }

  .title>p {
    width: 725px;
    margin: 0 auto;
  }

  .title i {
    color: #fff;
  }

  .menu {
    width: 180px;
    padding: 10px;
  }

  .menu a {
    display: block;
    font-size: 16px;
    color: #777;
    padding: 15px 10px;
  }

  .content {
    width: 650px;
    margin: 24px auto;
  }


  .bar {
    padding: 8px 0;
    justify-content: space-between;
  }

  .bar>div:first-child{
    align-items: center;
  }

  .bar>div:first-child i {
    margin-right: 10px;
  }

  i.add-remark {
    color: #468df1;
    font-size: 24px;
  }

  .fixed-right {
    position: fixed;
    top: 148px;
    right: 30px;
    width: 220px;
  }

  .temp-menu {
    color: #777;
    background: #fff;
    font-size: 16px;
    line-height: 20px;
    box-shadow: 0 1px 4px rgba(0,0,0,.04);
    border: 1px solid rgba(0,0,0,.09);
  }

  .temp-menu>div{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    border-bottom: 1px solid #eee;
    padding: 14px;
    border-left: 3px solid rgba(0,0,0,0);
  }

  .temp-menu>div.active {
    border-left-color: #468df1;
  }

  /*.temp-menu span {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 1px solid #03d7a3;
    text-align: center;
  }

  .temp-menu span::before {
    content: '+';
    font-size: 14px;
    font-weight: 600;
    color: #03d7a3;
  }

  .temp-menu span.active {
    border: 1px solid red;
  }

  .temp-menu span.active::before {
    content: '-';
    font-size: 14px;
    font-weight: 600;
    color: red;
  }*/

  
  .back-btn {
    margin-top: 8px;
    text-align: center;
    display: block;
    padding: 14px 0;
    background: #fff;
    color: #468df1;
    font-weight: 600;
    box-shadow: 0 1px 4px rgba(0,0,0,.04);
    border: 1px solid rgba(0,0,0,.09);
  }

  .hint {
    width: 100%;
    text-align: center;
    color: #777;
  }


  .cards {
    display: flex;
    flex-wrap: wrap;
  }

  .card {
    background: #fff;
    margin: 12px 0;
    padding: 25px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
    font-size: 16px;
    line-height: 32px;
  }

  .card:hover {
    box-shadow: 0 2px 8px 0 rgba(0,0,0,.14), 0 3px 4px -5px rgba(0,0,0,.2), 0 1px 8px 0 rgba(0,0,0,.12);
  }

  .card-header {
    border-bottom: 1px solid #f3f3f3;
    /*padding-bottom: 8px;*/
  }

  .card-header h3 {
    font-size: 22px;
    line-height: 48px;
    color: #777;
    font-weight: 400;
  }

  .button.tools {
    background: #eee;
    border: 1px solid #d4d4d4;
  }

  .button.tools>i {
    margin-left: 8px;
  }

  .card-main {
    line-height: 48px;
    /*background: #fafafa;*/
    /*margin: 10px;*/
    /*border-radius: 2px;*/
    /*border: 1px solid #e0e0e0;*/
    /*box-shadow: 0 1px 2px rgba(0,0,0,.04);*/
    /*max-height: 120px;*/
    /*overflow-y: hidden;*/
    font-size: 16px;
    cursor: pointer;
  }

  .main-record {
    display: inline-block;
    width: 170px;
  }

  .card-remark.text{
    padding: 4px 20px;
    margin: 10px 0 12px;
    border-left: 3px solid #3cb6ec;
    line-height: 2;
    font-size: 16px;
    color: rgba(0,0,0,.64);
  }

  .card-footer {
    text-align: right;
  }

  .card.base-info {
    line-height: 48px;
  }

  .add-record-area {
    margin-top: 35px;
  }

  .dropdown-menu li {
    cursor: pointer;
    padding: 0px 8px;
  }

  .dropdown-menu li:hover {
    background: #eee;
  }

  .list-enter-active, .list-leave-active {
    transition: all .5s;
  }
  .list-enter, .list-leave-active {
    opacity: 0;
    transform: translateX(30px);
  }

  .fade-enter-active, .fade-leave-active {
    transition: all .2s ease-in;
  }
  .fade-enter, .fade-leave-active {
    opacity: 0;
  }

  @media screen and (max-width: 1024px) {
    .content {
      width: 650px;
      margin: 24px 0 0 100px;
    }
  }

  @media screen and (max-width: 770px) {
    .content {
      width: 500px;
      margin: 24px 0 0 20px;
    }

    .fixed-right {
      width: 200px;
    }
  }
</style>


