<template>
  <div id="avatar">
    <mynav></mynav>
    <div class="content flex-row">
      <div class="profile">
        <div class="user-info">
          <div class="avatar"><span>{{ $root.shortName | uppercase }}</span></div>
          <div class="text flex-col" style="flex: 1;">
            <span class="name">{{ $root.userInfo.s_username }}</span>
            <span class="work">{{ $root.userInfo.s_workunti }}</span>
          </div>
        </div>
        <div class="link-list flex-col">
          <router-link to="/profile" class="link-item">个人信息</router-link>
          <router-link to="/avatar" class="link-item active">头像修改</router-link>
          <router-link to="/resetpw" class="link-item">密码修改</router-link>
        </div>
      </div>
      <div class="chang-pw">
        <h2>修改头像</h2>

        <div class="avatar">
          <img :src="'http://' + $root.userInfo.s_avatar" alt="" class="image-btn">
          <div class="add-avatar">
            <input id="my-file" accept="image/jpeg, image/jpg, image/png" multiple="multiple" name="file" type="file" @change="readURL">
            <label for="my-file">上传头像</label>
          </div>
        </div>

        <div class="flex-row" style="padding: 24px 10px 0 10px;">
          <span style="width: 180px;"></span>
          <div class="tools">
            <m-button type="blue" @click="changePw">确定</m-button>
          </div>
        </div>
           
      </div>
    </div>
  </div>
</template>
<script>
  import API from '../api.js'
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'
  import { resizeMe, dataUrlTOBlod } from '../utils/tools.js'

  export default {
    name: 'Avatar',
    components: {
      MButton,
      Mynav
    },
    data () {
      return {
        imgurl: ''
      }
    },
    methods: {
      // 预览, 压缩, 上传
      readURL (event) {
        let vm = this
        if (event.target.files && event.target.files[0]) {
          let reader = new window.FileReader()

          reader.onload = function (e) {
            /** 压缩 */
            let fileSize = event.target.files[0].size / 1024
            // scale 压缩比例
            let scale = (fileSize > 2500 && 0.1) || (fileSize > 1500 && 0.2) || (fileSize > 1000 && 0.3) || (fileSize > 500 && 0.6) || 0.8
            let img = new window.Image()
            img.src = e.target.result
            /** 上传 */
            img.onload = function () {
              // have to wait till it's loaded
              let dataURL = resizeMe(img, scale)
              vm.uploadImg(dataURL)
            }
          }

          reader.readAsDataURL(event.target.files[0])
        }
      },
      // 上传
      uploadImg (image) {
        let formData = new window.FormData()
        let blob = dataUrlTOBlod(image)
        formData.append('UploadForm[file]', blob)
        // ajax uplaod iamges
        API.PutImage(formData).then(response => {
          window.localStorage.setItem('avatarupload', response.ufileurl)
          this.imgurl = response.ufileurl
        }).catch(err => {
          window.alert('上传失败，重新上传' + err)
        })
      }
    }
  }
</script>
<style scoped>
  .content{
    margin: 50px auto;
    justify-content: center;
  }

  .profile {
    width: 216px;
    margin-right: 24px;
    height: 300px;
    background: #fafafa;
    /*padding: 24px 12px;*/
    box-shadow: 3px 3px 5px rgba(0, 0, 0, .13);
  }


  .profile .user-info {
    text-align: center;
    border-bottom: 1px solid #eee;
    margin: 24px 12px 0 12px;
  }

  .profile .avatar span{
    display: inline-block;
    font-size: 22px;
    height: 72px;
    width: 72px;
    line-height: 72px;
    background: #468df1;
    color: #fff;
    border-radius: 50%;
  }

  .profile .user-info .text {
    padding: 12px 0;
  }

  .profile .user-info .name {
    font-size: 18px;
    line-height: 24px;
  }

  .profile .user-info .work {
   font-size: 14px;
   line-height: 24px;
 }

 .profile .link-list {
   padding: 12px 0;
   font-size: 18px;
   line-height: 24px;
 }

 .profile .link-list a{
   font-size: 18px;
   line-height: 2;
   padding-left: 12px;
 }

 .profile .link-list a.active {
   background: #ccc;
 }

 .chang-pw {
   background: #fff;
   padding: 24px;
   width: 650px;
   border: 1px solid #ccc;
   align-items: center;
 }

  .chang-pw h2 {
    border-bottom: 1px solid #eee;
    font-size: 22px;
    font-weight: 400;
    color: #777;
    padding-bottom: 24px;
  }
 .option {
   padding: 10px;
   border-bottom: 1px solid #eee;
 }
 .option>label{
    line-height: 32px;
  }

  .chang-pw input[type="password"] {
    flex: 1;
    height: 30px;
    line-height: 30px;
    border-radius: 2px;
  }

  .hint {
    color: darkred;
  }

  .tools{
    flex: 1;
    width: 100%;
    text-align: left;
    cursor: pointer;
  }
  .tools>span:first-child{
    margin-right: 24px;
  }
</style>


