<template>
  <div>
    <mynav></mynav>
    
    <div class="list">
      <div class="option flex-row" v-for="item in notices">
        <span v-text="item.content"></span>
        <div class="date">{{ item.createtime | formatDate }}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from '../api.js'
  import Mynav from '../components/Header.vue'
  import MButton from '../lib/Button.vue'

  export default {
    name: 'Notices',
    components: {
      Mynav,
      MButton
    },
    data () {
      return {
        notices: []
      }
    },
    mounted () {
      this.getNotices()
    },
    methods: {
      getNotices () {
        api.GetNotices({userid: this.$root.userid}).then(res => {
          res.map(n => this.notices.push(n))
        }).catch(error => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>
  .list {
    width: 600px;
    margin: 20px auto;
    background: #fff;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, .1);
    padding: 10px;
  }

  .list .option {
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid #eee;
    letter-spacing: 1px;
  }
</style>


